import InputScreen from "../../common/InputScreen";
import {Link, Typography} from "@mui/material";
import CopyButton from "./CopyButton";
import MyModal from "../../common/MyModal";

const SupportModal = ({show, onClose, id}) => {
  return (
    <MyModal open={show} onClose={onClose}>
      <InputScreen
        title={`Поддержка`}
        buttons={
          <div style={{display: 'flex', flexDirection: 'row', justifyContent: 'center', width: '100%', gap: '24px'}}>
            <Typography color="text.disabled">
              Телеграм <Link underline="none" href="https://t.me/aispaysupport">@aispaysupport</Link>
            </Typography>
          </div>
        }
      >
        <Typography color="textSecondary">
          При обращении в поддержку указывайте номер заявки, чек и иную дополнительную информацию, которая поможет
          разобраться в ситуации.
        </Typography>

        <div>
          <Typography variant="body1" color="textSecondary">
            ID заявки
          </Typography>
          <div className="payment-copy-line">
            <Typography variant="h6" color="textSecondary">
              {id}
            </Typography>
            <CopyButton value={id}/>
          </div>
        </div>
      </InputScreen>
    </MyModal>
  )
}

export default SupportModal;