import {Typography, useTheme} from "@mui/material";
import toFixedFloat from "../../../../helpers/toFixedFloat";

const Conversion = ({ conversion }) => {
  const theme = useTheme();

  if (conversion >= 80) {
    return <Typography color={theme.palette.success.main}>
      {toFixedFloat(conversion)}{conversion !== undefined ? "%" : ""}
    </Typography>
  } else if (60 <= conversion && conversion < 80) {
    return <Typography color={theme.palette.warning.main}>
      {toFixedFloat(conversion)}{conversion !== undefined ? "%" : ""}
    </Typography>
  } else {
    return <Typography color={theme.palette.error.main}>
      {toFixedFloat(conversion)}{conversion !== undefined ? "%" : ""}
    </Typography>
  }
}

export default Conversion