import { useTheme } from "@emotion/react";
import { Paper } from "@mui/material";

const PaperButton = ({ children, isActive, onClick }) => {
  const theme = useTheme();

  const paperStyles = {
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',

    borderRadius: "8px",
    width: "100%",
    boxShadow: "none",
    padding: "10px 20px",
    fontSize: theme.typography.body1,
    color: isActive ? theme.palette.text : theme.palette.text.disabled,
    transition: "background-color 0.3s, color 0.3s, filter 0.3s",
    "&:hover": {
      cursor: "pointer",
      filter: "brightness(1.3)",
    },
  };

  return (
    <Paper elevation={isActive ? 5 : 1} sx={paperStyles} onClick={onClick}>
      {children}
    </Paper>
  );
};

export default PaperButton;
