import { ThemeProvider } from "@mui/material/styles";
import { CssBaseline, Paper } from "@mui/material";
import { Login, Admin, Payment, ProtectedRoute, ErrorPage, Requests, Users, Accounts, AutoPayments } from "./routes";
import { createBrowserRouter, Navigate, RouterProvider } from 'react-router-dom';
import { useMode, ColorModeContext } from "./theme";
import { SidebarProvider } from "./routes/Admin/context/SidebarContext";

import "./styles/app.css";
import "./styles/header.css";
import "./styles/login.css";
import "./styles/panel.css";
import "./styles/payment.css";
import "./styles/stats.css";
import Tokens from "./routes/Admin/tables/Tokens";

function App() {
  const [theme, colorMode] = useMode();

  const router = createBrowserRouter([
    { path: "/", element:
        <ProtectedRoute>
          <Admin/>
        </ProtectedRoute>,
      children: [
        { path: "", element: <Navigate to="requests"/>},
        { path: "requests", element: <Requests/>},
        { path: "users", element: <Users/>},
        { path: "accounts", element: <Accounts/>},
        { path: "autoPayments", element: <AutoPayments/>},
        { path: "tokens", element: <Tokens/>}
      ],
      errorElement: <ErrorPage/> },
    { path: "/login", element: <Login/> },
    { path: "/payment", element: <Payment/>, errorElement: <ErrorPage/>}
  ]);

  return (
    <ColorModeContext.Provider value={colorMode}>
      <ThemeProvider theme={theme}>
        <SidebarProvider>
          <CssBaseline />

          <Paper elevation={1} className="app-container" sx={{ boxShadow: 'none', borderRadius: 0 }}>
            <RouterProvider router={router}/>
          </Paper>

        </SidebarProvider>
      </ThemeProvider>
    </ColorModeContext.Provider>
  );
}

export default App;
