import { WideTextField, WideButton } from "../../../common"
import { useState } from "react";
import { getUser } from "../../../hooks/user.actions";
import { useNavigate } from "react-router-dom";
import {IconButton} from "@mui/material";
import {TextField, InputAdornment} from "@mui/material";
import { VisibilityOff, Visibility } from "@mui/icons-material";
import axiosService from "../../../helpers/axios";
import InputScreen from "../../../common/InputScreen";
import handleError from "../../../helpers/handleError";

const SettingsForm = ({ onClose }) => {
  const navigate = useNavigate();
  const user = getUser();

  const [form, setForm] = useState(user);
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [serverError, setServerError] = useState(null);

  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword(!showPassword);

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
    setErrors({});
    setServerError(null);
  }

  const validate = () => {
    let error = {};

    // Implement

    setErrors(error);
    return Object.keys(error).length === 0; 
  }

  const handleSubmit = async (event) => {
    setServerError(null);

    event.preventDefault();
    
    if (validate()) {
      setLoading(true);

      const data = {
        id: form.id,
        ...(form.email !== user.email && { email: form.email }),
        ...(form.password !== "" && { password: form.password }),
      }
  
      try {
        await axiosService.patch(`/users/${user.id}/`, data);
        onClose();
      } catch (error) {
        handleError(error, setServerError, navigate)
      }

      setLoading(false);
    }
  };

  return (
    <InputScreen 
      title={`Настройки`}
      onSubmit={handleSubmit}
      error={serverError}
      onErrorDismiss={() => setServerError(null)}
      buttons={
        <WideButton 
          color="primary" 
          type="submit" 
          loading={loading} 
          disabled={JSON.stringify(user) === JSON.stringify(form)}
        >
          Сохранить
        </WideButton>
      }
    >
      <div className="text-fields">
        <WideTextField 
          label="Логин"
          name="email"
          type="email"
          value={form.email || ''}
          error={!!errors.full_name || !!serverError}
          helperText={errors.full_name}
          onChange={handleChange}
        />
        <TextField
          label="Новый пароль" 
          name="password"
          error={!!errors.password || !!serverError}
          helperText={errors.password}
          onChange={handleChange}
          type={showPassword ? "text" : "password"}
          fullWidth
          inputProps={{
            autoComplete: 'new-password',
            style: {
              borderTopRightRadius: "unset",
              borderBottomRightRadius: "unset",
            },
          }}
          InputProps={{
            style: { borderRadius: 8 },
            endAdornment: (
              <InputAdornment
                position="end"
                style={{ marginLeft: 5, marginRight: 5 }}
              >
                <IconButton onClick={handleClickShowPassword} edge="end">
                  {showPassword ? (
                    <VisibilityOff style={{ opacity: 0.2 }} />
                  ) : (
                    <Visibility style={{ opacity: 0.2 }} />
                  )}
                </IconButton>
              </InputAdornment>
            ),
          }}
        />
      </div>

    </InputScreen>
  );
};

export default SettingsForm;
