import { useState } from "react";
import { useTheme } from "@emotion/react";
import {WideTextField, WideButton, PasswordTextField} from "../../../common";
import { useUserActions } from "../../../hooks/user.actions";
import { Typography } from "@mui/material";
import InputScreen from "../../../common/InputScreen";

const Login = () => {
  const theme = useTheme();
  const userActions = useUserActions();

  const [form, setForm] = useState({ email: "", password: "" });
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [serverError, setServerError] = useState(null);

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
    setErrors({});
    setServerError(null);
  };

  const validate = () => {
    let error = {};

    if (!form.email.trim()) {
      error.email = "Email is required";
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(form.email)) {
      error.email = "Invalid email format";
    }
    if (!form.password.trim()) {
      error.password = "Пароль обязателен.";
    }

    setErrors(error);
    return Object.keys(error).length === 0;
  };

  const handleSubmit = async (event) => {
    setServerError(null);

    event.preventDefault();

    if (validate()) {
      setLoading(true);

      userActions.login(form).catch((error) => {
        if (error.response && error.response.status === 401) {
          setServerError("Неправильный логин или пароль"); // Unauthorized
        } else {
          setServerError("Ошибка: " + error);
        }
        setLoading(false);
      });
    }
  };

  return (
    <div className="container">
      <InputScreen 
        title="Вход в систему"
        subtitle="Шлюз переводов для вашего бизнеса"
        onSubmit={handleSubmit}
        error={serverError}
        onErrorDismiss={() => setServerError(null)}
        buttons={
          <div className="buttons" style={{ flexDirection: "column" }}>
            <WideButton color="primary" type="submit" loading={loading}>
              Войти
            </WideButton>
            <Typography variant="body2" color="text.disabled">
              Вы ещё не с нами?{" "}
              <a style={{ color: theme.palette.primary.main, textDecoration: "none" }} href="https://example.com">
                Присоединяйтесь
              </a>
            </Typography>
          </div>
        }
      >
        <div className="body">
          <WideTextField
            label="Почта"
            name="email"
            type="email"
            error={!!errors.email || !!serverError}
            helperText={errors.email} 
            onChange={handleChange}
          />
          
          <PasswordTextField
            label="Пароль"
            name="password"
            error={!!errors.password || !!serverError}
            helperText={errors.password}
            fullWidth
            onChange={handleChange}
          />
        </div>
      </InputScreen>
    </div>
  );
};

export default Login;
