import { Alert, IconButton, Collapse } from "@mui/material";
import { Close } from "@mui/icons-material";

const AlertError = ({ title, error, onDismiss }) => {
  return (
    <Collapse in={error}>
      <Alert
        variant="filled"
        severity="error"
        style={{ marginBottom: '20px' }}
        action={
          <IconButton
            aria-label="close"
            color="inherit"
            size="small"
            onClick={onDismiss}
          >
            <Close fontSize="inherit" />
          </IconButton>
        }
      >
        {title}
      </Alert>
    </Collapse>
  );
};

export default AlertError;
