import { Typography } from "@mui/material";
import {useState, useEffect, useContext} from "react";
import { useLocation } from "react-router-dom";
import { EditPopup } from "../modals";
import axiosService from "../../../helpers/axios";
import UpdatesContext from "../context/UpdatesContext";
import ActionTab from "../components/ActionTab";
import DataTable from "./DataTable";
import {pages} from "../../../data/pages";
import {getUser} from "../../../hooks/user.actions";

const TablePage = ({ title, columns, filters }) => {
  const userType = getUser().user_type;
  const page = useLocation().pathname.slice(1);
  const paramsDefaults = {
    search: "",
    page: 1,
    page_size: 15,
    filter: filters[0]
  }
  const [count, setCount] = useState(0);
  const [data, setData] = useState([]);

  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [showNewRequests, setShowNewRequests] = useState(true);
  const [params, setParams] = useState(paramsDefaults);
  const [rowData, setRowData] = useState({});

  const [showEditPopup, setShowEditPopup] = useState(false);
  const toggleEditPopup = () => setShowEditPopup(!showEditPopup);

  const handleRowClick = (clickedRow) => {
    setRowData(clickedRow)
    toggleEditPopup()
  };

  const { newStatuses, emptyRequests, emptyStatuses } = useContext(UpdatesContext);

  // TODO: бля эта хуйня бесконечно будет обновляться
  useEffect(() => {
    // в data найти эти заявки и изменять datу

    setData(data.map(item => {
      const foundIndex = newStatuses.findIndex(newItem => newItem.request_proprietary_id === item.request_proprietary_id);
      return foundIndex !== -1 ? newStatuses[foundIndex] : item;
    }))

    // emptyStatuses()

  }, [newStatuses])

  const fetchData = async () => {
    setError(null);
    setIsLoading(true);

    try {
      // Параметрами передать page = tablePage, id = значение для фильтра
      const response = await axiosService.get(`/${page}/`, {
        params: {
          page: params.page,
          page_size: params.page_size,
          ...(params.search && { search: params.search }),
          ...params.filter.value
        }
      });
      setData(response.data.results);

      if(params.page === 1 && page === "requests") {
        emptyRequests();
      }

      setCount(response.data.count);
    } catch (error) {
      setError(error)
      setShowNewRequests(true);
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    setParams(paramsDefaults)
  }, [page]);

  useEffect(() => {
    fetchData();
  }, [params.search, params.page, params.page_size, params.filter]);

  return (
    <div className="page">
      <Typography variant="h5">{title}</Typography>

      <div style={{ display: 'flex', flexDirection: 'column', gap: '24px', flex: 1}}>
        <div style={{ display: 'flex', flexDirection: 'column', gap: '16px'}}>
          <ActionTab
            filters={filters}
            params={params}
            onParamsChange={setParams}
            showBadge={showNewRequests}
            onUpdateClick={() => {
              setParams({...params, page: 1})
              fetchData()
            }}
            onCreated={fetchData}
          />
        </div>
        <DataTable
          rows={data}
          columns={columns}
          onRowClick={pages[page].canClickRow.includes(userType) ? handleRowClick : undefined}
          count={Math.ceil(count / params.page_size)}
          tablePage={params.page}
          onPageChange={
            (newTablePage) =>
              setParams({...params, page: newTablePage})
          }
          error={error}
          loading={isLoading}
          onRefresh={fetchData}
        />
      </div>

      <EditPopup
        show={showEditPopup}
        onClose={() => {
          toggleEditPopup();
          fetchData();
        }}
        data={rowData}
      />
    </div>
  );
};

export default TablePage;
