import { Paper, Typography } from "@mui/material";
import { useState, useEffect } from "react";
import { getUser } from "../../../hooks/user.actions";
import axiosService from "../../../helpers/axios";
import formatMoney from "../../../helpers/formatMoney";
import { Skeleton } from '@mui/material'

const Stats = () => {
  const user = getUser();

  const [loading, setLoading] = useState(true);

  // Данные
  const [data, setData] = useState({
    amount_on_demand: 0,
    balance: 0,
    deals: 0,
    income: 0,
    trader: 0, 
    turnover: 0
  });

  const fetchStats = async () => {
    setLoading(true);

    try {
      const response = await axiosService.get(`/stats/`);
      setData(response.data);
      setLoading(false);
    } catch (error) {
      console.log(error)
    }
  };

  useEffect(() => {
    const ws = new WebSocket(process.env.REACT_APP_SOCKET_URL + `/ws/${user.id}/stats/`);
    ws.onopen = () => {
      console.log("Connected to stats!")
    };
    ws.onmessage = (event) => {
      let data = JSON.parse(event.data)

      if(data.type === "balance update") {
        setData(JSON.parse(event.data).message);
      }
    };
    return () => {
      ws.close();
    };

  }, [user.id])

  useEffect(() => {
    fetchStats()
  }, [])

  const statsDict = [
    { id: "balance", label: "Общий баланс"},
    ...(!["merchant"].includes(user.user_type) ? [
      { id: "income", label: 'Прибыль'},
    ]: [
      { id: "fee", label: "Комиссия" }
    ]),
    { id: "deals", label: 'Сделки'},
    { id: "turnover", label: 'Оборот'}
  ]

  return (
    <div className="stats">
      {statsDict.map((stat) => (
        <Paper key={stat.id} className="stat" elevation={4}>
          <Typography variant="body1"  color="text.disabled">{stat.label}</Typography>
          <Typography variant="h5">{loading ? <Skeleton animation="wave" /> : formatMoney(Math.floor(data[stat.id]))}</Typography>
        </Paper>
      ))}
    </div>
  );
};

export default Stats;
