import {useContext} from "react";
import { useNavigate, useLocation } from "react-router-dom";
import { getUser } from "../../../hooks/user.actions";
import UpdatesContext from "../context/UpdatesContext";
import PaperButton from "./PaperButton";
import Shifts from "./Shifts";
import {Badge, Divider} from "@mui/material";
import {pages} from "../../../data/pages";
import {users} from "../../../data/users";

const PageSelector = ({ onChange }) => {
  const navigate = useNavigate();
  const location = useLocation().pathname;

  const user = getUser();
  const userPages = users[user.user_type].pages;

  const { newRequests } = useContext(UpdatesContext);

  const handleClick = (page) => {
    navigate(`${page}`);

    // Проверяем передали ли значение
    if (typeof onChange === 'function') {
      onChange();
    }
  }

  return (
    <div className="page-selector">
      {userPages.map(p => (
        <PaperButton
          isActive={`/${p}` === location}
          onClick={() => handleClick(p)}
          key={p}
        >
          {pages[p].label}
          <Badge
            color="warning"
            badgeContent={newRequests.length}
            invisible={newRequests.length <= 0 || p !== "requests"}
            style={{ borderRadius: '3px' }}
          />
        </PaperButton>
      ))}
      {user.user_type === "trader" &&
        <>
          <Divider/>
          <Shifts/>
        </>
      }
    </div>
  );
};

export default PageSelector;
