import React, {useState, createContext, useEffect} from "react";
import {getUser} from "../../../hooks/user.actions";

const UpdatesContext = createContext(undefined);
export default UpdatesContext;

export const UpdatesProvider = ({children}) => {
  const user = getUser();

  const [newRequests, setNewRequests] = useState([]);
  const [newStatuses, setNewStatuses] = useState([]);

  useEffect(() => {
    const ws = new WebSocket(process.env.REACT_APP_SOCKET_URL + `/ws/${user.id}/requests/`);
    ws.onopen = () => {
      console.log("Connected to requests!")
    };
    ws.onmessage = (event) => {
      let data = JSON.parse(event.data)

      if(data.type === "new request") {
        setNewRequests((prevRequests) => [...prevRequests, data.message])
      }
      if(data.type === "request update") {
        setNewStatuses([...newStatuses, data.message])
      }
    };
    return () => {
      ws.close();
    };

  }, [user.id])

  const emptyRequests = () => {
    setNewRequests([]);
  }

  const emptyStatuses = () => {
    setNewStatuses([]);
  }

  return (
    <UpdatesContext.Provider value={{
      newRequests,
      newStatuses,
      emptyRequests,
      emptyStatuses
    }}>
      {children}
    </UpdatesContext.Provider>
  )
}