import {Account, Bank} from "./formatting";
import {getUser} from "../../../hooks/user.actions";
import AccountStatus from "./formatting/AccountStatus";
import Conversion from "./formatting/Conversion";
import TablePage from "./TablePage";

const Accounts = () => {
  const userType = getUser()?.user_type

  const filters = [
    { label: 'Все', value: { is_deleted: 'false' } },
    { label: 'Статистика', value: { has_stats: 'true' }},
    { label: 'В работе', value: { is_deleted: 'false', is_active: 'true' } },
    { label: 'Отключен', value: { is_deleted: 'false', is_active: 'false' } },
    { label: 'Архив', value: { is_deleted: 'true' }},
  ]

  const columns = [
    { label: 'ID', element: (row) => row.id },
    ...(userType !== "trader" ? [
      { label: 'Логин', element: (row) => row.trader },
    ]: []),
    { label: 'Номер', element: (row) => row.phone_number },
    { label: 'Реквизиты', element: (row) => <Account card_number={row.card_number} full_name={row.full_name}/>},
    { label: 'Банк', element: (row) => <Bank bank_name={row.bank_name}/> },
    { label: 'Статус', element: (row) => <AccountStatus is_active={row.is_active}/>},
    { label: 'Статистика', element: (row) => row.deals > 0 ? `${row.deals}/${row.turnover}` : ""},
    { label: 'Конверсия', element: (row) => <Conversion conversion={row.conversion}/>},
  ]

  return (
    <TablePage
      title="Реквизиты"
      filters={filters}
      columns={columns}
    />
  );
};

export default Accounts;