import { useTheme } from "@emotion/react";
import { Table, TableRow, TableCell, TableBody, TableContainer, Pagination, LinearProgress } from "@mui/material";
import TableError from "./TableError";

const DataTable = ({ rows, columns, onRowClick, loading, error, onRefresh, onPageChange, count, tablePage }) => {
  const theme = useTheme();

  if (error) {
    return (
      <div style={{display: 'flex', flexDirection: 'column', gap: '20px', flex: 1}}>
        <TableError error={error} onRefresh={onRefresh}/>
      </div>
    )
  }

  return (
    <div style={{display: 'flex', flexDirection: 'column', gap: '20px', flex: 1 }}>
      <TableContainer>
        <Table>
          <TableBody>
            <TableRow>
              {columns.map((column) => {
                const width = column.width ? column.width : "100px"

                return (
                  <TableCell
                    key={column.label}
                    className='table-cell'
                    sx={{ borderBottom: "1px solid #F7F8FB0A" }}
                    style={{ minWidth: width, color: theme.palette.text.disabled }}
                  >
                    {column.label}
                  </TableCell>
                )
              })}
            </TableRow>

            <TableRow>
              <TableCell
                colSpan={columns.length}
                style={{ borderBottom: "none", padding: 0 }}
                sx={{ borderBottom: "1px solid #F7F8FB0A" }}
              >
                <LinearProgress style={{ visibility: loading ? "visible" : "hidden" }} />
              </TableCell>
            </TableRow>

            {rows.map((row, i) => (
              <TableRow
                key={i}
                onClick={onRowClick && (() => onRowClick(row))}
                hover={onRowClick && true}
                style={onRowClick && { cursor: "pointer" }}
              >
                {columns.map((column) => {
                  const width = column.width ? column.width : "100px"

                  return (
                    <TableCell
                      key={`${column.label}`}
                      className='table-cell'
                      style={{ minWidth: width, maxWidth: width }}
                      sx={{ borderBottom: "1px solid #F7F8FB0A" }}
                    >
                      {column.element(row)}
                    </TableCell>
                  );
                })}
              </TableRow>
            ))}

          </TableBody>
        </Table>
      </TableContainer>
      <div style={{ display: 'flex', justifyContent: 'end', paddingRight: '3px' }}>
        <Pagination
          count={count}
          page={tablePage}
          shape="rounded"
          onChange={(event, newTablePage) => {
            onPageChange(newTablePage)
            window.scrollTo({ top: 0, behavior: 'smooth' });
            // window.scrollTo(0, 0);
          }}
        />
      </div>
    </div>
  );
};

export default DataTable;
