import {Typography, useTheme} from "@mui/material";
import {banks} from "../../../../data/banks";

const Bank = ({ bank_name }) => {
  const theme = useTheme();

  if (!bank_name) {
    return ""
  }

  const { label, color } = banks[bank_name];

  return (
    <Typography color={color(theme)}>
      {label}
    </Typography>
  );
};

export default Bank;
