import { Paper, Typography } from '@mui/material';
import '../styles/login.css';
import AlertError from "./AlertError";

const InputScreen = ({ title, subtitle, children, error, onErrorDismiss, buttons, ...props }) => {
  return (
    <Paper elevation={4} className="input-screen" component="form" style={{gap: buttons ? "40px" : "15px"}} {...props}>
      <div className="title">
        <Typography variant="h4">{title}</Typography>
        {subtitle && (
          <Typography variant="body2" color="text.disabled">
            {subtitle}
          </Typography>
        )}
      </div>
      
      <div>
        <AlertError
          title={error}
          error={Boolean(error)}
          onDismiss={onErrorDismiss}
        />
        <div className="body">
          {children}
        </div>
      </div>

      {buttons && (
        <div className="buttons">
          {buttons}
        </div>
      )}
    </Paper>
  );
};

export default InputScreen;
