import {getUser} from "../../../hooks/user.actions";
import {formatTime} from "../../../helpers/formatTime";
import TablePage from "./TablePage";

const AutoPayments = () => {
  const userType = getUser()?.user_type

  const filters = [
    { label: 'Все', value: {} }
  ]

  const columns = [
    { label: 'ID', element: (row) => row.id },
    ...(["admin", "operator"].includes(userType) ? [
      { label: 'Логин', element: (row) => row.trader_email },
    ]: []),
    { label: 'Дата', element: (row) => formatTime(row.date) },
    { label: 'СМС', element: (row) => row.sms },
  ]

  return (
    <TablePage
      title="Автоплатежи"
      filters={filters}
      columns={columns}
    />
  );
};

export default AutoPayments;