import {useLocation} from "react-router-dom";
import {CreateUser, CreateBankAccount} from "../forms";
import MyModal from "../../../common/MyModal";
import CreateToken from "../forms/CreateToken";

const CreatePopup = ({show, onClose}) => {
  const page = useLocation().pathname.slice(1);

  return (
    <MyModal open={show} onClose={onClose}>
      {page === "users" && <CreateUser onClose={onClose}/>}
      {page === "accounts" && <CreateBankAccount onClose={onClose}/>}
      {page === "tokens" && <CreateToken onClose={onClose}/>}
    </MyModal>
  );
};

export default CreatePopup;
