import {useLocation} from "react-router-dom";
import {EditUser, EditBankAccount, EditRequest} from "../forms";
import MyModal from "../../../common/MyModal";

const EditPopup = ({show, onClose, data}) => {
  const page = useLocation().pathname.slice(1);

  return (
    <MyModal open={show} onClose={onClose}>
      {page === "users" && <EditUser onClose={onClose} data={data}/>}
      {page === "accounts" && <EditBankAccount onClose={onClose} data={data}/>}
      {page === "requests" && <EditRequest onClose={onClose} data={data}/>}
    </MyModal>
  );
};

export default EditPopup;
