import { Typography } from '@mui/material'
import toFixedFloat from "../../../../helpers/toFixedFloat";

const Amount = ({ amount_initial, amount_to_pay }) => {
  const amount0 = toFixedFloat(amount_initial)
  const amount1 = toFixedFloat(amount_to_pay)

  if (amount0 === amount1) {
    return <Typography>{amount0}</Typography>
  }

  return (
    <>
      <Typography color="text.disabled" style={{ textDecoration: "line-through" }} >{amount0}</Typography>
      <Typography>{amount1}</Typography>
    </>
  )
}

export default Amount