import InputScreen from "../../../../common/InputScreen";
import axiosService from "../../../../helpers/axios";
import {WideButton, WideTextField} from "../../../../common";
import {FormControl, FormHelperText, InputLabel, MenuItem, Select} from "@mui/material";
import {useState} from "react";
import {useNavigate} from "react-router-dom";
import handleError from "../../../../helpers/handleError";

const EditBalanceForm = ({ selectedRow, onClose }) => {
  const defaultValues = {
    amount: '',
    choice: '',
  };

  const navigate = useNavigate();
  const [loading, setLoading] = useState(false);
  const [serverError, setServerError] = useState(null);
  const [errors, setErrors] = useState({});
  const [form, setForm] = useState(defaultValues);

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
    setErrors({});
    setServerError(null);
  };

  const handleRefill = async (event) => {
    event.stopPropagation();
    event.preventDefault();

    setLoading(true);

    try {
      await axiosService.post(`/users/balance/`, {
        amount: form.amount,
        choice: form.choice,
        user: selectedRow.id
      });
      onClose();
    } catch (error) {
      handleError(error, setServerError, navigate);
    }

    setLoading(false);
  }

  return (
    <InputScreen
      title={"Изменение баланса"}
      onSubmit={handleRefill}
      error={serverError}
      onErrorDismiss={() => setServerError(null)}
      buttons={
        <WideButton color="primary" type="submit" loading={loading}>
          Подтвердить
        </WideButton>
      }
    >
      <FormControl fullWidth error={!!errors.choice || !!serverError}>
        <InputLabel>Операция</InputLabel>
        <Select
          label="Операция"
          name="choice"
          value={form.choice}
          onChange={handleChange}
          sx={{ borderRadius: '8px' }}
        >
          <MenuItem value={"REFILL"}>Пополнение</MenuItem>
          <MenuItem value={"WITHDRAWAL"}>Вывод</MenuItem>
        </Select>
        <FormHelperText>{errors.choice}</FormHelperText>
      </FormControl>

      <WideTextField
        label="Сумма"
        name="amount"
        value={form.amount}
        error={!!errors.amount || !!serverError}
        helperText={errors.amount}
        onChange={handleChange}
      />
    </InputScreen>
  );
};

export default EditBalanceForm;