import {CircularProgress} from "@mui/material";
import LoadingButton from "@mui/lab/LoadingButton";
import {Pause, PlayArrow, Stop} from "@mui/icons-material";
import {useContext} from "react";
import SessionContext from "../context/SessionContext";

const Shifts = () => {
  const { shiftState, startShift, pauseShift, stopShift } = useContext(SessionContext);

  const firstButton = {
    0: {
      color: "warning",
      startIcon: <Pause/>,
      onClick: pauseShift,
      text: "Приостановить"
    },
    1: {
      color: "primary",
      startIcon: <PlayArrow/>,
      onClick: startShift,
      text: "Возобновить"
    },
    2: {
      color: "success",
      startIcon: <PlayArrow/>,
      onClick: startShift,
      text: "Начать сессию"
    }
  }

  if(shiftState === null) {
    return (
      <LoadingButton
        disabled
        className="button"
        startIcon={<PlayArrow/>}
        disableElevation
        style={{justifyContent: "flex-start", fontSize: "16px"}}
        loadingIndicator={<CircularProgress color="inherit" size={25} />}
        sx={{textTransform: "none", padding: "8px 20px", borderRadius: "8px"}}
        loading={true}
      >
        Начать сессию
      </LoadingButton>
    )
  }

  return (
    <>
      <LoadingButton
        color={firstButton[shiftState].color}
        className="button"
        startIcon={firstButton[shiftState].startIcon}
        disableElevation
        style={{justifyContent: "flex-start", fontSize: "16px"}}
        loadingIndicator={<CircularProgress color="inherit" size={25} />}
        sx={{textTransform: "none", padding: "8px 20px", borderRadius: "8px"}}
        onClick={firstButton[shiftState].onClick}
      >
        {firstButton[shiftState].text}
      </LoadingButton>

      {shiftState === 1 && (
        <LoadingButton
          color="error"
          className="button"
          startIcon={<Stop/>}
          disableElevation
          style={{justifyContent: "flex-start", fontSize: "16px"}}
          loadingIndicator={<CircularProgress color="inherit" size={25} />}
          sx={{textTransform: "none", padding: "8px 20px", borderRadius: "8px"}}
          onClick={stopShift}
        >
          Закончить
        </LoadingButton>
      )}
    </>
  )
}

export default Shifts