import {Collapse, Divider, Typography} from "@mui/material";
import React from "react";
import {
  Timeline,
  TimelineConnector,
  TimelineContent,
  TimelineDot,
  TimelineItem,
  TimelineOppositeContent, timelineOppositeContentClasses,
  TimelineSeparator
} from "@mui/lab"
import {statuses} from "../../../data/statuses";
import {getDate} from "../../../helpers/getDate";
import {getTime} from "../../../helpers/getTime";

const History = ({ history }) => {
  return (
    <>
      {/*<Divider/>*/}
      {/*<Typography variant="h6">*/}
      {/*  История*/}
      {/*</Typography>*/}

      <Collapse in={history.length > 0}>
        <Timeline style={{ margin: 0, padding: 0 }} sx={{
          [`& .${timelineOppositeContentClasses.root}`]: {
            flex: 0.2,
          },
        }}>
          {history.map((state, i) => (
            <TimelineItem key={i}>
              <TimelineOppositeContent color="textSecondary">
                <Typography>
                  {getDate(state.updated)}
                </Typography>
                <Typography>
                  {getTime(state.updated)}
                </Typography>
              </TimelineOppositeContent>
              <TimelineSeparator>
                <TimelineDot variant="outlined" />
                {i < history.length - 1 && <TimelineConnector />}
              </TimelineSeparator>
              <TimelineContent>
                {state.message ?
                  Object.keys(JSON.parse(state.message)).map((key, index) => (
                    <React.Fragment key={index}>
                      <Typography>
                        {key === "status" && `Изменение статуса`}
                        {key === "amount" && `Изменение суммы`}
                      </Typography>
                      <Typography color="textSecondary">
                        {key === "status" &&
                          `${statuses[JSON.parse(state.message)[key][0]].translation} → ${statuses[JSON.parse(state.message)[key][1]].translation}`
                        }
                        {key === "amount" &&
                          `${JSON.parse(state.message)[key][0]} → ${JSON.parse(state.message)[key][1]}`
                        }
                      </Typography>
                      {state?.user && (
                        <Typography color="text.disabled">
                          {state?.user}
                        </Typography>
                      )}
                    </React.Fragment>
                  )) :
                    <>
                      <Typography>
                        Изменение статуса
                      </Typography>
                      <Typography color="textSecondary">
                        {statuses[state.status].translation}
                      </Typography>
                    </>
                }
              </TimelineContent>
            </TimelineItem>
          ))}
        </Timeline>
      </Collapse>
    </>
  );
};

export default History;
