import { useState, useEffect } from "react";
import { WideTextField, WideButton } from "../../../common";
import { FormControl, Select, InputLabel, MenuItem } from "@mui/material";
import { getUser } from "../../../hooks/user.actions";
import { useNavigate } from "react-router-dom";
import axiosService from "../../../helpers/axios";
import toFixedFloat from "../../../helpers/toFixedFloat";
import {banks} from "../../../data/banks";
import {statuses} from "../../../data/statuses";
import handleError from "../../../helpers/handleError";
import InputScreen from "../../../common/InputScreen";

const EditRequest = ({ data, onClose }) => {
  const navigate = useNavigate();
  const user = getUser();
  const userType = user.user_type;

  const [form, setForm] = useState(data);
  // const [history, setHistory] = useState([]);
  const [loading, setLoading] = useState(false);
  const [approveLoading, setApproveLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [serverError, setServerError] = useState(null);
  
  useEffect(() => {
    setForm(data);
    // fetchHistory();
  }, [data]);

  function formatCardNumber(number) {
    const formattedNumber = number.replace(/\D/g, '').slice(0, 16);
    const groups = formattedNumber.match(/.{1,4}/g);
    return groups.join(' ');
  }

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
    setErrors({});
    setServerError(null);
  }

  // const fetchHistory = async () => {
  //   try {
  //     const response = await axiosService.get(`/requests/stats/${data.id}/`);
  //     setHistory(response.data)
  //   } catch (error) {
  //     console.log(error);
  //   }
  // }

  const validate = () => {
    let error = {};

    // Implement

    setErrors(error);
    return Object.keys(error).length === 0; 
  }

  const handleSave = async (event) => {
    setServerError(null);
    setLoading(true);

    event.preventDefault();

    if (validate()) {
      setLoading(true);

      try {
        await axiosService.patch(`/requests/${data.id}/`, {
          amount_to_pay: parseFloat(form.amount_to_pay),
        });
        onClose()
      } catch (error) {
        handleError(error, setServerError, navigate)
      }

      setLoading(false);
    }
  };

  const handleApprove = async (event) => {
    setServerError(null);
    setApproveLoading(true);

    event.preventDefault();

    try {
      await axiosService.post(`/requests/approve/`, {
        request_proprietary_id: data.request_proprietary_id,
      });
      onClose();
    } catch (error) {
      handleError(error, setServerError, navigate)
    }

    setApproveLoading(true);
  };

  return (
    <InputScreen
      title={"Заявка"}
      subtitle={data.request_proprietary_id}
      onSubmit={handleSave}
      error={serverError}
      onErrorDismiss={() => setServerError(null)}
      buttons={
        <>
          <WideButton color="primary" type="submit" loading={loading}
                      disabled={JSON.stringify(data) === JSON.stringify(form)}>Сохранить</WideButton>
          <WideButton color="success" onClick={handleApprove} loading={approveLoading}
                      disabled={!statuses[data.status].canConfirm}>Подтвердить</WideButton>
        </>
      }
    >
      <div className="text-fields">
        <WideTextField
          label="Счёт"
          disabled
          value={form.card_number ? formatCardNumber(form.card_number) : ''}
        />
        {userType !== "merchant" && (
          <FormControl fullWidth>
            <InputLabel>Банк</InputLabel>
            <Select
              label="Банк"
              value={form.bank_name || ''}
              disabled
              sx={{ borderRadius: '8px' }}
            >
              {Object.keys(banks).map(bank_name => (
                <MenuItem key={bank_name} value={bank_name}>
                  {banks[bank_name].label}
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        )}
      </div>

      <WideTextField
        label="ФИО"
        disabled
        value={form.full_name || ''}
      />

      <FormControl fullWidth>
        <InputLabel>Статус</InputLabel>
        <Select
          label="Статус"
          name="currency"
          value={form.status}
          disabled
          sx={{ borderRadius: '8px' }}
        >
          {Object.keys(statuses).map(status => (
            <MenuItem key={status} value={status}>
              {statuses[status].label}
            </MenuItem>
          ))}
        </Select>
      </FormControl>

      {["admin", "operator"].includes(user.user_type) && (
        <>
          <div className="text-fields">
            <WideTextField
              label="Трейдер"
              disabled
              value={form.trader_email || ''}
            />
            <WideTextField
              label="Партнёр"
              disabled
              value={form.merchant_email || ''}
            />
          </div>
          <div className="text-fields">
            <WideTextField
              label="Прибыль"
              disabled
              value={toFixedFloat(form.profit) || ''}
            />

            <WideTextField
              label="Убыток"
              disabled
              value={toFixedFloat(form.loss) || ''}
            />
          </div>
        </>
      )}

      <div className="text-fields">
        <WideTextField
          label="Изначальная сумма"
          disabled
          name="initial_amount"
          value={form.initial_amount || ''}
          error={!!errors.initial_amount}
          helperText={errors.initial_amount}
          onChange={handleChange}
        />

        <WideTextField
          label="К оплате"
          disabled={!statuses[form.status].canChangeAmount || userType === "merchant"}
          name="amount_to_pay"
          value={form.amount_to_pay || ''}
          error={!!errors.amount_to_pay}
          helperText={errors.amount_to_pay}
          onChange={handleChange}
        />
      </div>
    </InputScreen>
);
};

export default EditRequest;

    