import {User, UserActivity} from "./formatting";
import toFixedFloat from "../../../helpers/toFixedFloat";
import {Button} from "@mui/material";
import Conversion from "./formatting/Conversion";
import React, {useState} from "react";
import TablePage from "./TablePage";
import MyModal from "../../../common/MyModal";
import EditBalanceForm from "./formatting/EditBalanceForm";

const Users = () => {
  const filters = [
    { label: 'Все', value: {} },
    { label: 'Партнёры', value: { user_type: 'merchant' } },
    { label: 'Трейдеры', value: { user_type: 'trader' } }
  ]

  const columns = [
    { label: 'ID', element: (row) => row.id },
    { label: 'Логин', element: (row) => row.email },
    { label: 'Тип', element: (row) => <User type={row.user_type}/> },
    { label: 'Баланс', element: (row) => toFixedFloat(row?.balance) },
    { label: 'Активность', element: (row) => <UserActivity isOnline={row.is_online}/> },
    { label: 'Конверсия', element: (row) => <Conversion conversion={row.conversion}/>},
    { label: 'Действие', element: (row) =>
      ["trader", "merchant"].includes(row.user_type) &&
        <Button
          variant="outlined"
          size="small"
          disableElevation
          sx={{ textTransform: "none" }}
          onClick={(event) => {
            event.stopPropagation();

            setSelectedRow(row);
            toggleShowEditBalance();
          }}
        >
          Изменить баланс
        </Button>
    }
  ]

  const [selectedRow, setSelectedRow] = useState(null);

  const [showEditBalance, setShowEditBalance] = useState(false);
  const toggleShowEditBalance = () => {
    setShowEditBalance(prev => !prev);
  }

  return (
    <>
      <MyModal open={showEditBalance} onClose={toggleShowEditBalance}>
        <EditBalanceForm selectedRow={selectedRow} onClose={() => {
          toggleShowEditBalance();
          window.location.reload();
        }}/>
      </MyModal>

      <TablePage
        title="Пользователи"
        filters={filters}
        columns={columns}
      />
    </>
  );
};

export default Users;