import { useState } from "react";
import { FormControl, InputLabel, Select, MenuItem, FormHelperText } from "@mui/material";
import { WideTextField, WideButton } from "../../../common";
import { useNavigate } from "react-router-dom";
import axiosService from "../../../helpers/axios";
import InputScreen from "../../../common/InputScreen";
import {banks} from "../../../data/banks";
import {currencies} from "../../../data/currencies";
import handleError from "../../../helpers/handleError";

const CreateBankAccount = ({ onClose }) => {
  const defaultValues = { 
    account_number: '',
    card_number: '',
    phone_number: '',
    bank_name: '',
    currency: '',
    full_name: '',
  }
  const navigate = useNavigate();
  const [serverError, setServerError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [form, setForm] = useState(defaultValues);

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
    dropErrors();
  };

  const dropErrors = () => {
    setErrors({});
    setServerError(null);
  }

  const handleBankingChange = (event) => {
    const formattedGroups = event.target.value.replace(/\D/g, "").match(/.{1,4}/g);
    const finalCardNumber = formattedGroups ? formattedGroups.join(" ") : "";
    setForm({ ...form, [event.target.name]: finalCardNumber });
    dropErrors();
  };

  const handlePhoneNumberChange = (event) => {
    const formattedPhoneNumber = event.target.value.replace(/[^+\d]/g, "");
    setForm({ ...form, phone_number: formattedPhoneNumber });
    dropErrors();
  };

  const validate = () => {
    let error = {};

    if (!form.account_number.trim()) {
      error.account_number = "Обязательное поле.";
    } else if (form.account_number < 20) {
      error.password = "Минимум 20 символов.";
    }
    if (!form.card_number.trim()) {
      error.card_number = "Обязательное поле.";
    } else if (form.card_number.replace(/\s/g, '').length < 16) {
      error.card_number = "Минимум 16 символов.";
    }
    
    if (!form.bank_name.trim()) {
      error.bank_name = "Обязательное поле.";
    }
    if (!form.currency.trim()) {
      error.currency = "Обязательное поле.";
    }

    if (!form.phone_number.trim()) {
      error.phone_number = "Обязательное поле.";
    } else if (!form.phone_number.startsWith("+")) {
      error.phone_number = "Номер телефона должен начинаться с +.";
    } else if (form.phone_number.length < 10) {
      error.phone_number = "Номер телефона должен содержать минимум 10 цифр.";
    }

    if (!form.full_name.trim()) {
      error.full_name = "Обязательное поле.";
    }

    setErrors(error);
    return Object.keys(error).length === 0; 
  }

  const handleSubmit = async (event) => {
    setServerError(null);

    event.preventDefault();

    if (validate()) {
      setLoading(true);

      let data = { 
        account_number: Number(form.account_number.trim()),
        card_number: Number(form.card_number.trim().replace(/\s/g, "")),
        phone_number: `${form.phone_number.trim()}`,
        bank_name: form.bank_name.trim(),
        currency: form.currency.trim(),
        full_name: form.full_name.trim()
      }
      
      try {
        await axiosService.post(`/accounts/`, data);
        onClose();
      } catch (error) {
        handleError(error, setServerError, navigate)
      }

      setLoading(false);
    }
  };

  return (
    <InputScreen
      title="Новый реквизит"
      onSubmit={handleSubmit}
      error={serverError}
      onErrorDismiss={() => setServerError(null)}
      buttons={
        <WideButton 
          color="primary"
          type="submit"
          loading={loading}
        >
          Создать
        </WideButton>
      }
    >
      <WideTextField 
        label="Номер банковского счёта"
        name="account_number"
        value={form.account_number}
        error={!!errors.account_number || !!serverError}
        helperText={errors.account_number} 
        onChange={handleChange}
        inputProps={{ maxLength: 20 }}
      />
      
      <WideTextField 
        label="Номер карты"
        name="card_number"
        value={form.card_number}
        error={!!errors.card_number || !!serverError}
        helperText={errors.card_number}
        onChange={handleBankingChange}
        inputProps={{ maxLength: 19 }}
      />

      <div className="text-fields">
        <FormControl fullWidth error={!!errors.bank_name || !!serverError}>
          <InputLabel>Банк</InputLabel>
          <Select
            label="Банк"
            name="bank_name" 
            value={form.bank_name}
            onChange={handleChange}
            sx={{ borderRadius: '8px' }}
          >
            {Object.keys(banks).map(bank_name => (
              <MenuItem key={bank_name} value={bank_name}>
                {banks[bank_name].label}
              </MenuItem>
            ))}
          </Select>
          <FormHelperText>{errors.bank_name}</FormHelperText>
        </FormControl>

        <FormControl fullWidth error={!!errors.currency || !!serverError}>
          <InputLabel>Валюта</InputLabel>
          <Select
            label="Валюта"
            name="currency"
            value={form.currency}
            error={!!errors.currency || !!serverError}
            onChange={handleChange}
            sx={{ borderRadius: '8px' }}
          >
            {Object.keys(currencies).map(currency => (
              <MenuItem key={currency} value={currency}>
                {currencies[currency]}
              </MenuItem>
            ))}
          </Select>
          <FormHelperText>{errors.currency}</FormHelperText>
        </FormControl>
      </div>
            
      <WideTextField 
        label="Номер телефона"
        name="phone_number"
        type="tel"
        value={form.phone_number}
        error={!!errors.phone_number || !!serverError}
        helperText={errors.phone_number}
        onChange={handlePhoneNumberChange}
        inputProps={{ maxLength: 16 }}
        style={{ flex: 1 }}
      />

      <WideTextField 
        label="ФИО"
        name="full_name" 
        error={!!errors.full_name || !!serverError}
        helperText={errors.full_name}
        onChange={handleChange}
      />
    </InputScreen>
  );
};

export default CreateBankAccount;
