import {Typography} from "@mui/material";
import {useMode} from "../../../../theme";
import toNum from "../../../../helpers/toNum";

const Fee = ({profit, loss}) => {
  const [theme] = useMode();

  return (
    <Typography>
      <span style={{color: theme.palette.success.main}}>{toNum(profit)}</span>
      {loss ? (
        <>
          <span> / </span>
          <span style={{color: theme.palette.error.main}}>{toNum(loss)}</span>
        </>
      ) : <></>}
    </Typography>
  );
};

export default Fee;
