import { useState } from "react";
import { WideTextField, WideButton } from "../../../common";
import { useNavigate } from "react-router-dom";
import axiosService from "../../../helpers/axios";
import InputScreen from "../../../common/InputScreen";
import {FormControl, FormHelperText, InputLabel, MenuItem, Select, Typography} from "@mui/material";
import CopyButton from "../../Payment/CopyButton";
import handleError from "../../../helpers/handleError";

const CreateToken = ({ onClose }) => {
  const defaultValues = {
    name: '',
  }
  const navigate = useNavigate();
  const [serverError, setServerError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [form, setForm] = useState(defaultValues);

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
    dropErrors();
  };

  const [response, setResponse] = useState(null);

  const dropErrors = () => {
    setErrors({});
    setServerError(null);
  }

  const validate = () => {
    let error = {};

    setErrors(error);
    return Object.keys(error).length === 0;
  }

  const handleSubmit = async (event) => {
    setServerError(null);

    event.preventDefault();

    if (validate()) {
      setLoading(true);

      let data = {
        name: form.name.trim(),
        timedelta: form.timedelta
      }

      try {
        let response = await axiosService.post(`/tokens/`, data);
        setResponse(response.data)
      } catch (error) {
        handleError(error, setServerError, navigate);
      }

      setLoading(false);
    }
  };

  return (
    <InputScreen
      title="Новый API токен"
      onSubmit={handleSubmit}
      error={serverError}
      onErrorDismiss={() => setServerError(null)}
      buttons={!response ?
        <WideButton
          color="primary"
          type="submit"
          loading={loading}
        >
          Создать
        </WideButton> :
        <WideButton
          color="primary"
          loading={loading}
          onClick={onClose}
        >
          Круто, спасибо
        </WideButton>
      }
    >
      {
        !response ?
          <>
            <WideTextField
              label="Название"
              name="name"
              value={form.name}
              error={!!errors.name || !!serverError}
              helperText={errors.name}
              onChange={handleChange}
              inputProps={{ maxLength: 20 }}
            />
            <FormControl fullWidth error={!!errors.timedelta || !!serverError}>
              <InputLabel>Срок жизни</InputLabel>
              <Select
                label="Срок жизни"
                name="timedelta"
                value={form.timedelta}
                onChange={handleChange}
                sx={{ borderRadius: '8px' }}
              >
                <MenuItem value={1}>1 день</MenuItem>
                <MenuItem value={7}>1 неделя</MenuItem>
                <MenuItem value={30}>1 месяц</MenuItem>
                <MenuItem value={90}>3 месяца</MenuItem>
              </Select>
              <FormHelperText>{errors.timedelta}</FormHelperText>
            </FormControl>
          </>
           :
          <>
            <Typography color="textSecondary">
              Скопируйте ваш новый токен, потому что после закрытия этого окна, его нельзя будет посмотреть снова.
            </Typography>
            <div className="payment-copy-line">
              <Typography variant="h5" style={{ whiteSpace: 'nowrap', overflow: 'hidden', textOverflow: 'ellipsis' }}>
                {response.token}
              </Typography>
              <CopyButton value={response.token}/>
            </div>
          </>
      }

    </InputScreen>
  );
};

export default CreateToken;
