import MyModal from "../../common/MyModal";
import InputScreen from "../../common/InputScreen";
import {WideButton} from "../../common";
import axios from "axios";
import {useState} from "react";

const CancelConfirmation = ({show, onClose, id}) => {
  const [serverError, setServerError] = useState(null);
  const [loading, setLoading] = useState(false);

  const handleCancel = async (event) => {
    setLoading(true);

    event.preventDefault();

    try {
      await axios.post(`/requests/cancel/`, {
        request_proprietary_id: id
      }, { baseURL: process.env.REACT_APP_API_URL });
      onClose();
    } catch (error) {
      console.log(error)
    }

    setLoading(false);
  };

  return (
    <MyModal open={show} onClose={onClose}>
      <InputScreen
        title={"Отмена"}
        onSubmit={handleCancel}
        error={serverError}
        onErrorDismiss={() => setServerError(null)}
        buttons={
          <>
            <WideButton type="submit" loading={loading}>Отменить</WideButton>
          </>
        }
      >
        Если вы отмените заявку и отправите денежные средства по реквезитам, данную ситуацию можно будет решить только через поддержку сервиса.
      </InputScreen>
    </MyModal>
  )
}

export default CancelConfirmation;


