import {useContext} from 'react';
import { Outlet } from 'react-router-dom';
import SidebarContext from "./context/SidebarContext";
import Header from "../../layout/Header";
import Sidebar from "./Sidebar/Sidebar";
import MyDrawer from "./Sidebar/MyDrawer";
import Stats from "./components/Stats";
import {UpdatesProvider} from "./context/UpdatesContext";
import { SessionProvider } from './context/SessionContext';
import {getUser} from "../../hooks/user.actions";

const Admin = () => {
  const { showDrawer, showSidebar, toggleSidebar } = useContext(SidebarContext);
  const userType = getUser().user_type

  return (
    <UpdatesProvider>
      <Header menu/>
      <SessionProvider>
        <Sidebar show={showSidebar}/>
        <MyDrawer showDrawer={showDrawer} onToggle={toggleSidebar}/>
      </SessionProvider>

      <div className="panel" style={{ ...(showSidebar && { marginLeft: "260px" })}}>
        {
          userType !== "operator" && <Stats/>
        }

        <Outlet/>
      </div>
    </UpdatesProvider>
  );
};

export default Admin;
