import { createContext, useState, useMemo } from "react";
import { createTheme } from "@mui/material";

export const themeSettings = (mode) => {
  return {
    palette: {
      mode: mode,
      ...(mode === "dark")
        ? {
          primary: {
            main: "#8A8CD9",
            light: "#8A8CD97A"
          },
          secondary: {
            main: "#59A8D4",
            light: "#59A8D47A",
          },
          success: {
            main: "#4AA785",
            light: "#4AA7857A",
            contrastText: "#fff"
          },
          info: {
            main: "#3e6479"
          },
          error: {
            main: "#C13030",
            light: "#C130307A"
          },
          warning: {
            main: "#FFC555",
            light: "#FFC5557A"
          },
          bebra: {
            main: "#702a2a",
            light: "#611b1b",
            dark: "#591313",
            contrastText: "#fff"
          }
        }
        : {

        }
    },
    typography: {
      fontFamily: [
        'Inter',
        'Roboto',
      ].join(','),
    },
    components: {
      MuiInputBase: {
        styleOverrides: {
          root: {
            "&:has(> input:-webkit-autofill)": {
              backgroundColor: "#44466a",
            },
          },
        },
      },
      MuiOutlinedInput: {
        styleOverrides: {
          input: {
            "&:-webkit-autofill": {
              "WebkitBoxShadow": "0 0 0 100px #44466a inset",
              // "-webkit-text-fill-color": "var(--text-primary)",
            },
          },
        },
      },
    },
  }
}

export const ColorModeContext = createContext({
  toggleColorMode: () => {},
});

export const useMode = () => {
  const savedMode = localStorage.getItem("themeMode") || "dark";
  const [mode, setMode] = useState(savedMode);

  const colorMode = useMemo(
    () => ({
      toggleColorMode: () => {
        setMode(prevMode => {
          const newMode = prevMode === "light" ? "dark" : "light";
          
          localStorage.setItem("themeMode", newMode);
          return newMode;
        });
      },
    }), []
  );

  const theme = useMemo(() => createTheme(themeSettings(mode)), [mode, themeSettings]);
  return [theme, colorMode];
};