import React, {useState, createContext, useEffect} from "react";
import { useNavigate } from "react-router-dom";
import axiosService from "../../../helpers/axios";
import { getUser } from "../../../hooks/user.actions";

const SessionContext = createContext(undefined);
export default SessionContext;

export const SessionProvider = ({children}) => {
  const navigate = useNavigate();
  const user = getUser();
  const userType = user.user_type;

  const [shiftState, setShiftState] = useState(null);

  const fetchShift = async () => {
    try {
      // Параметрами передать page = tablePage, id = значение для фильтра
      const response = await axiosService.get(`/users/shifts/`);
      setShiftState(response.data.status);
    } catch (error) {
      console.log(error)
    }
  };

  useEffect(() => {
    if(userType === "trader") {
      fetchShift();
    }
  }, [userType]);

  const changeShift = async (status) => {
    try {
      const response = await axiosService.patch(`/users/shifts/`, {status: status});
      setShiftState(response.data.status);
    } catch (error) {
      if (error.response && error.response.status === 401) {
        navigate("/login");  // надеюсь работает
      } else {
        console.log(error)
      }
    }
  };

  const startShift = async () => {
    try {
      const response = await axiosService.post(`/users/shifts/`);
      setShiftState(response.data.status);
    } catch (error) {
      if (error.response && error.response.status === 401) {
        navigate("/login");  // надеюсь работает
      } else {
        console.log(error)
      }
    }
  }

  const pauseShift = () => {
    changeShift(1);
  }

  const stopShift = () => {
    changeShift(2);
  }

  return (
    <SessionContext.Provider value={{
      shiftState,
      startShift,
      pauseShift,
      stopShift

    }}>
      {children}
    </SessionContext.Provider>
  )
}