import { useState } from "react";
import { FormControl, InputLabel, Select, MenuItem, Collapse, FormHelperText } from "@mui/material";
import { WideTextField, WideButton } from "../../../common";
import {TextField, InputAdornment, IconButton} from "@mui/material";
import { useNavigate } from "react-router-dom";
import axiosService from "../../../helpers/axios";
import InputScreen from "../../../common/InputScreen";
import {Visibility, VisibilityOff} from "@mui/icons-material";
import {users} from "../../../data/users";
import handleError from "../../../helpers/handleError";

const CreateUser = ({ onClose }) => {
  const defaultValues = { 
    email: '',
    password: '',
    user_type: '',
    refill_percent: '',
    withdraw_percent: '',
    balance: ''
  }
  const navigate = useNavigate();
  const [serverError, setServerError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [form, setForm] = useState(defaultValues);

  const [showPassword, setShowPassword] = useState(false);
  const handleClickShowPassword = () => setShowPassword(!showPassword);

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
    setErrors({});
    setServerError(null);
  }

  const validate = () => {
    let error = {};

    // Добавить проверки (в зависимости от пользователя)
    if (!form.email.trim()) {
      error.email = "Обязательное поле";
    } else if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(form.email)) {
      error.email = "Неверный формат электронной почты";
    }

    if (!form.password.trim()) {
      error.password = "Обязательное поле.";
    } else if (form.password.length < 8) {
      error.password = "Не менее 8 символов.";
    }

    if (!form.user_type.trim()) {
      error.user_type = "Обязательное поле";
    }

    if (["merchant", "trader"].includes(form.user_type)) {
      if (!form.refill_percent.trim()) {
        error.refill_percent = "Обязательное поле.";
      }
  
      if (!form.withdraw_percent.trim()) {
        error.withdraw_percent = "Обязательное поле";
      }
    }

    // Проверить баланс
    if (form.user_type === "trader") {
      if (!form.balance || !/^\d+$/.test(form.balance)) {
        error.balance = "Balance must be a valid number";
      }
    }

    setErrors(error);
    return Object.keys(error).length === 0; 
  }

  const handleSubmit = async (event) => {
    setServerError(null);

    event.preventDefault();

    if (validate()) {
      setLoading(true);

      let data = {
        email: form.email,
        password: form.password,
        user_type: form.user_type,
        ...(["trader"].includes(form.user_type) && { balance: form.balance }),
        ...(["trader", "merchant"].includes(form.user_type) && { 
          refill_percent: parseFloat(form.refill_percent),
          withdraw_percent: parseFloat(form.withdraw_percent)
        })
      }

      try {
        await axiosService.post(`/users/`, data);
        onClose();
      } catch (error) {
        handleError(error, setServerError, navigate)
      }
      
      setLoading(false)
    }
  };

  return (
    <InputScreen
      title="Новый пользователь"
      onSubmit={handleSubmit}
      error={serverError}
      onErrorDismiss={() => setServerError(null)}
      buttons={
        <WideButton color="primary" type="submit" loading={loading}>Создать</WideButton>
      }
    >
      <div className="text-fields">
        <WideTextField 
          label="Логин"
          name="email"
          error={!!errors.email || !!serverError}
          helperText={errors.email}
          onChange={handleChange}
        />
        <TextField
          label="Пароль" 
          name="password"
          error={!!errors.password || !!serverError}
          helperText={errors.password}
          onChange={handleChange}
          type={showPassword ? "text" : "password"}
          fullWidth
          inputProps={{
            autoComplete: 'new-password',
            style: {
              borderTopRightRadius: "unset",
              borderBottomRightRadius: "unset",
            },
          }}
          InputProps={{
            style: { borderRadius: 8 },
            endAdornment: (
              <InputAdornment
                position="end"
                style={{ marginLeft: 5, marginRight: 5 }}
              >
                <IconButton onClick={handleClickShowPassword} edge="end">
                  {showPassword ? (
                    <VisibilityOff style={{ opacity: 0.2 }} />
                  ) : (
                    <Visibility style={{ opacity: 0.2 }} />
                  )}
                </IconButton>
              </InputAdornment>
            ),
          }}
        />
      </div>
      
      <div>
        <FormControl fullWidth error={!!errors.user_type || !!serverError}>
          <InputLabel>Тип пользователя</InputLabel>
          <Select
            label="Тип пользователя"
            name="user_type"
            value={form.user_type}
            onChange={handleChange}
            sx={{ borderRadius: '8px' }}
          >
            {Object.keys(users).map(user_type => (
              <MenuItem key={user_type} value={user_type}>
                {users[user_type].label}
              </MenuItem>
            ))}
          </Select>
          <FormHelperText>{errors.user_type}</FormHelperText>
        </FormControl>

        <Collapse in={["merchant", "trader"].includes(form.user_type)}>
          <div className="text-fields" style={{ marginTop: "20px" }}>
            <WideTextField 
              label="Процент приёма"
              name="refill_percent"
              error={!!errors.refill_percent || !!serverError}
              helperText={errors.refill_percent}
              onChange={handleChange}
            />
            <WideTextField
              label="Процент выплаты" 
              name="withdraw_percent" 
              error={!!errors.withdraw_percent || !!serverError}
              helperText={errors.withdraw_percent}
              onChange={handleChange}
            />
          </div>
        </Collapse>

        <Collapse in={form.user_type === "trader"}>
          <WideTextField 
            label="Баланс"
            name="balance"
            error={!!errors.balance || !!serverError}
            helperText={errors.balance}
            style={{ marginTop: "20px" }}
            onChange={handleChange}
          />
        </Collapse>
      </div>
    </InputScreen>
  );
};

export default CreateUser;
