const handleError = ( error, setServerError, navigate ) => {
  console.log(error);

  if (error.response && error.response.status === 401) {
    navigate("/login");
  } else if (error.response && error.response.status === 400) {
    setServerError("Неверный запрос. Проверьте данные.");
  } else if (error.response && error.response.status === 500) {
    setServerError("Ошибка сервера. Повторите попытку позже.");
  } else {
    if (!error.response) {
      setServerError("Ошибка сети. Проверьте подключение к интернету.");
    } else {
      setServerError("Неизвестная ошибка: " + error.message);
      console.log(error);
    }
  }
}

export default handleError