import { WideTextField, WideButton } from "../../../common";
import { FormControl, Select, InputLabel, MenuItem, FormHelperText } from "@mui/material";
import { useState } from "react";
import { getUser } from "../../../hooks/user.actions";
import { useNavigate } from "react-router-dom";
import axiosService from "../../../helpers/axios";
import InputScreen from "../../../common/InputScreen";
import handleError from "../../../helpers/handleError";

const EditBankAccount = ({ data, onClose }) => {
  const navigate = useNavigate();
  const user = getUser();

  const [form, setForm] = useState(data);
  const [loading, setLoading] = useState(false);
  const [deleteLoading, setDeleteLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [serverError, setServerError] = useState(null);

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
    setErrors({});
    setServerError(null);
  }

  const handleBankingChange = (event) => {
    const formattedGroups = event.target.value.replace(/\D/g, "").match(/.{1,4}/g);
    const finalCardNumber = formattedGroups ? formattedGroups.join(" ") : "";
    setForm({ ...form, [event.target.name]: finalCardNumber });
    setErrors({});
    setServerError(null);
  };

  const validate = () => {
    let error = {};

    // TODO: Implement

    setErrors(error);
    return Object.keys(error).length === 0; 
  }

  const handleSubmit = async (event) => {
    setServerError(null);

    event.preventDefault();

    if (validate()) {
      setLoading(true);

      let duta = {
        ...(form.account_number !== data.account_number ? { account_number: parseInt(form.account_number.replace(/\s/g, "")) } : {}),
        ...(form.card_number !== data.card_number ? { card_number: parseInt(form.card_number.replace(/\s/g, "")) } : {}),
        ...(form.bank_name !== data.bank_name ? { bank_name: form.bank_name } : {}),
        ...(form.currency !== data.currency ? { currency: form.currency } : {}),
        ...(form.phone_number !== data.phone_number ? { phone_number: form.phone_number } : {}),
        ...(form.full_name !== data.full_name ? { full_name: form.full_name } : {}),
        ...(form.is_active !== data.is_active ? { is_active: form.is_active } : {}),
        ...(data.is_deleted ? { is_deleted: false }: {}),
      };

      try {
        await axiosService.patch(`/accounts/${data.id}/`, duta);  // надо в цифры переделывать а не строки
        onClose();
      } catch (error) {
        handleError(error, setServerError, navigate)
      }

      setLoading(false);
    }
  };

  const handleDelete = async (event) => {
    setServerError(null);
    setDeleteLoading(true);

    event.preventDefault();

    try {
      await axiosService.delete(`/accounts/${data.id}/`);
      onClose();
    } catch (error) {
      handleError(error, setServerError, navigate)
    }

    setDeleteLoading(false);
  }

  return (
    <InputScreen 
      title={`Реквизит ${data.id}`}
      onSubmit={handleSubmit}
      error={serverError}
      onErrorDismiss={() => setServerError(null)}
      buttons={
        <>
          <WideButton 
            color={!data.is_deleted ? "primary" : "warning"}
            type="submit"
            loading={loading}
            disabled={((JSON.stringify(data) === JSON.stringify(form)) && !data.is_deleted) || ["operator", "admin"].includes(user.user_type)}
          >
            {!data.is_deleted ? "Сохранить" : "Восстановить"}
          </WideButton>

          {!data.is_deleted &&
            <WideButton
              color="bebra"
              loading={deleteLoading}
              onClick={handleDelete}
              disabled={user.user_type !== "trader"}
            >
              Удалить
            </WideButton>
          }
        </>
      }
    >
      <WideTextField 
        label="Номер банковского счёта"
        name="account_number"
        value={form.account_number || ''}
        onChange={handleChange}
        disabled={user.user_type !== "trader" || data.is_deleted}
        error={!!errors.account_number || !!serverError}
        inputProps={{ maxLength: 20 }}
      />
      
      <WideTextField 
        label="Номер карты"
        name="card_number" 
        value={form.card_number || ''}
        onChange={handleBankingChange}
        disabled={user.user_type !== "trader" || data.is_deleted}
        error={!!errors.card_number || !!serverError}
        inputProps={{ maxLength: 19 }}
      />

      <div className="text-fields">
        <FormControl fullWidth error={!!errors.bank_name || !!serverError}>
          <InputLabel>Банк</InputLabel>
          <Select
            label="Банк"
            name="bank_name"
            value={form.bank_name}
            error={!!errors.currency || !!serverError}
            onChange={handleChange}
            disabled={user.user_type !== "trader" || data.is_deleted}
            sx={{ borderRadius: '8px' }}
          >
            <MenuItem value={"SBR"}>Сбербанк</MenuItem>
            <MenuItem value={"TNK"}>Тинькофф</MenuItem>
            <MenuItem value={"RFI"}>Райффайзен</MenuItem>
            <MenuItem value={"ALF"}>Альфабанк</MenuItem>
          </Select>
        </FormControl>

        <FormControl fullWidth error={!!errors.currency || !!serverError}>
          <InputLabel>Валюта</InputLabel>
          <Select
            label="Валюта"
            name="currency"
            value={form.currency || 'RUB'}
            error={!!errors.currency || !!serverError}
            onChange={handleChange}
            disabled={user.user_type !== "trader" || data.is_deleted}
            sx={{ borderRadius: '8px' }}
          >
            <MenuItem value={"RUB"}>₽</MenuItem>
            <MenuItem value={"USD"}>$</MenuItem>
            <MenuItem value={"EUR"}>€</MenuItem>
            <MenuItem value={"BTC"}>฿</MenuItem>
            <MenuItem value={"JPY"}>¥</MenuItem>
          </Select>
          <FormHelperText>{errors.currency}</FormHelperText>
        </FormControl>
      </div>
            
      <WideTextField 
        label="Номер телефона" 
        name="phone_number"
        value={form.phone_number || ''}
        error={!!errors.phone_number || !!serverError}
        helperText={errors.phone_number}
        onChange={handleChange}
        disabled={user.user_type !== "trader" || data.is_deleted}
      />

      <WideTextField 
        label="ФИО"
        name="full_name" 
        value={form.full_name || ''}
        error={!!errors.full_name || !!serverError}
        helperText={errors.full_name}
        onChange={handleChange}
        disabled={user.user_type !== "trader" || data.is_deleted}
      />

      <FormControl error={!!errors.bank_name || !!serverError}>
        <InputLabel>Статус</InputLabel>
        <Select
          label="Статус"
          name="is_active"
          value={form.is_active !== undefined ? form.is_active : true}
          error={!!errors.is_active || !!serverError}
          disabled={user.user_type !== "trader" || data.is_deleted}
          onChange={handleChange}
          sx={{ borderRadius: '8px' }}
        >
          <MenuItem value={true}>В работе</MenuItem>
          <MenuItem value={false}>Отключен</MenuItem>
        </Select>
        <FormHelperText>{errors.is_active}</FormHelperText>
      </FormControl>
    </InputScreen>
  );
};

export default EditBankAccount;
