import { useUserActions } from "../../../hooks/user.actions";
import { SettingsPopup } from "../modals";
import { useState } from "react";
import { getUser } from "../../../hooks/user.actions";
import {Typography} from "@mui/material";
import PaperButton from "./PaperButton";

const BottomSettings = () => {
  const user = getUser()
  const userActions = useUserActions();
  
  const [showSettings, setShowSettings] = useState(false);
  const toggleShowSettings = () => setShowSettings(!showSettings);

  const handleLogout = () => {
    userActions
      .logout()
      .catch((e) => console.log(e.data?.detail | "An error occurred."));
  };

  return (
    <>
      <SettingsPopup
        show={showSettings}
        onClose={toggleShowSettings}
      />
      <div className="bottom-settings">
        <PaperButton onClick={toggleShowSettings}>
          <Typography overflow="hidden" textOverflow="ellipsis">
            {user.email}
          </Typography>
        </PaperButton>
        <PaperButton onClick={handleLogout}>Выход</PaperButton>
      </div>
    </>
  );
};

export default BottomSettings;
