import {
  Paper,
  Typography,
  CircularProgress,
  Tooltip,
} from '@mui/material'
import {useTheme} from '@emotion/react'
import {useState, useEffect} from 'react'
import {WideButton} from "../../common";
import formatCardNumber from "../../helpers/formatCardNumber";
import {statuses} from "../../data/statuses";
import Header from "../../layout/Header";
import CopyButton from "./CopyButton";
import {useTimer} from "../../hooks/useTimer";
import axios from "axios";
import toFixedFloat from "../../helpers/toFixedFloat";
import {HelpOutline} from "@mui/icons-material";
import {banks} from "../../data/banks";
import SecondaryWindows from "./SecondaryWindows";
import SupportModal from "./SupportModal";
import CancelConfirmation from "./CancelConfirmation";
import Timer from "./Timer";

const Payment = () => {
  const theme = useTheme();

  const [isLoading, setIsLoading] = useState(false);

  const searchParams = new URLSearchParams(window.location.search);
  const id = searchParams.get('id');

  const [request, setRequest] = useState({
    id: id,
    request_proprietary_id: "",
    status: Object.keys(statuses)[0],
    account: "",
    initial_amount: "",
    amount_to_pay: "",
    timer_start: Date.now(),
    full_name: ""
  })

  const amount0 = toFixedFloat(request.initial_amount)
  const amount1 = toFixedFloat(request.amount_to_pay)

  const timeLeft = useTimer(request.timer_start);

  const fetchData = async () => {
    try {
      // Параметрами передать page = tablePage, id = значение для фильтра
      const response = await axios.get(`/requests/${id}/`, { baseURL: process.env.REACT_APP_API_URL });
      setRequest(response.data);
    } catch (error) {
      console.log(error)
    }
  };

  useEffect(() => {
    fetchData()
  }, []);

  const handleConfirm = async () => {
    setIsLoading(true);

    const data = {
      request_proprietary_id: request.request_proprietary_id,
    }

    try {
      // Параметрами передать page = tablePage, id = значение для фильтра
      await axios.post(`/requests/confirm/`, data, { baseURL: process.env.REACT_APP_API_URL });
      await fetchData()
    } catch (error) {
      console.log(error)
    } finally {
      setIsLoading(false);
    }
  };

  useEffect(() => {
    const ws = new WebSocket(process.env.REACT_APP_SOCKET_URL + `/ws/request/${id}/`);
    ws.onopen = () => {
      console.log("Connected to request updates!")
    };
    ws.onmessage = (event) => {
      setRequest(JSON.parse(event.data).message)
    };
    return () => {
      ws.close();
    };

  }, [])

  const [showCancel, setShowCancel] = useState(false);
  const toggleShowCancel = () => {
    setShowCancel(prev => !prev)
  }

  const [show, setShow] = useState(false);
  const toggleSupport = () => {
    setShow(!show)
  }

  return (
    <>
      <Header/>
      <SupportModal show={show} onClose={toggleSupport} id={id}/>
      <CancelConfirmation show={showCancel} onClose={toggleShowCancel} id={request.request_proprietary_id}/>

      <div className='payment-container'>
        <div className="payment-content">
          <Paper elevation={4} className='payment-main-window'>
            <Typography variant="h4">
              {statuses[request.status].full_label}
            </Typography>

            <div className='payment-main-windows'>
              <Paper elevation={10} className='payment-main-paper'>
                {request?.card_number
                  ? (
                    <>
                      <img src={banks[request.bank_name].img} alt={request.bank_name} style={{width: '154px'}}/>
                      <div>
                        <Typography variant="body1" color="textSecondary">
                          Реквизиты для перевода
                        </Typography>
                        <div className="payment-copy-line">
                          <Typography variant="h5">
                            {formatCardNumber(request.card_number)}
                          </Typography>
                          <CopyButton value={request.card_number}/>
                        </div>

                        <Typography variant="body1" color="text.disabled">
                          {request.full_name}
                        </Typography>
                      </div>
                      <div style={{display: 'flex', flexDirection: 'column', gap: '3px'}}>
                        <Typography variant="body1" color="textSecondary">
                          Сумма перевода
                        </Typography>
                        <div className="payment-copy-line">
                          <div style={{
                            display: 'flex',
                            flexDirection: 'row',
                            gap: '8px',
                            alignItems: 'center'
                          }}>
                            {amount0 === amount1
                              ? <Typography variant="h5">
                                  {amount0}
                                </Typography>
                              : (
                                <>
                                  <Typography
                                      variant="h5"
                                      color="text.disabled"
                                      style={{textDecoration: "line-through"}}>
                                    {amount0}
                                  </Typography>
                                  <Typography variant="h5">{amount1}</Typography>
                                  <Tooltip
                                    title="Сумма изменена в связи с высоким количеством заявок"
                                    componentsProps={{
                                      tooltip: {
                                        style: {
                                          maxWidth: 150,
                                          fontSize: "0.8rem",
                                        },
                                      },
                                    }}
                                  >
                                    <HelpOutline style={{color: theme.palette.warning.main}}></HelpOutline>
                                  </Tooltip>
                                </>
                              )
                            }
                          </div>

                          <CopyButton value={request.amount_to_pay}/>
                        </div>
                      </div>
                    </>
                  ) : (
                    <div style={{display: 'flex', alignItems: 'center', justifyContent: 'center', flex: 1}}>
                      <CircularProgress size={100}/>
                    </div>
                  )
                }
              </Paper>

              <Paper elevation={10} className='payment-main-paper'>
                {statuses[request.status].showTimer &&
                  <Timer timeLeft={timeLeft}/>
                }

                <Typography variant="body1" color="textSecondary">
                  {statuses[request.status].helpingText}
                </Typography>
              </Paper>
            </div>
            <div className='payment-buttons'>
              <div style={{flex: 1}}>
                {statuses[request.status].canCancel &&
                  <WideButton color="bebra" onClick={toggleShowCancel}>
                    Отменить
                  </WideButton>
                }
              </div>
              <div style={{flex: 1}}>
                {request.status === "WP" &&
                  <WideButton color='success' loading={isLoading} onClick={handleConfirm}>
                    Оплачено
                  </WideButton>
                }
                {statuses[request.status].canAskSupport &&
                  <WideButton color='info' onClick={toggleSupport}>
                    Поддержка
                  </WideButton>
                }
              </div>
            </div>
          </Paper>
          <SecondaryWindows/>
        </div>
      </div>
      <div className="payment-footer">
        <Typography color="text.disabled">
          @ 2024 aispay
        </Typography>
        <Typography color="text.disabled" onClick={toggleSupport} style={{ cursor: 'pointer '}}>
          Центр поддержки
        </Typography>
      </div>
    </>
  )
}

export default Payment