import { useLocation } from "react-router-dom";
import {IconButton, InputBase, Paper, useTheme} from '@mui/material';
import { useEffect, useState } from 'react';
import {Search} from "@mui/icons-material";

const DebounceSearch = ({ onChange }) => {
  const theme = useTheme()
  const [search, setSearch] = useState('');
  const [timeoutId, setTimeoutId] = useState(null);
  const page = useLocation().pathname.slice(1);

  useEffect(() => {
    setSearch('')
  }, [page]);

  const handleSearchChange = (event) => {
    setSearch(event.target.value);

    clearTimeout(timeoutId);

    setTimeoutId(setTimeout(() => {
      onChange(event.target.value);
    }, 500));
  };

  useEffect(() => {
    return () => clearTimeout(timeoutId);
  }, [timeoutId]);

  return (
    <Paper sx={{
      p: '2px 4px',
      display: 'flex',
      alignItems: 'center',
      borderRadius: '8px',
      boxShadow: "none",
      maxWidth: "272px", /* Maximum width on larger screens */
      width: "100%"
    }}>
      <InputBase
        sx={{
          ml: 1.5,
          flex: 1,
          "& .MuiInputBase-input": {
            "::placeholder": {
              color: theme.palette.text.disabled,
            },
          }
        }}
        placeholder="Поиск"
        inputProps={{ 'aria-label': 'Поиск' }}
        value={search}
        onChange={handleSearchChange}
      />
      <IconButton disabled type="button" sx={{ p: '5px' }}>
        <Search style={{ color: theme.palette.text.disabled }}/>
      </IconButton>
    </Paper>
  )
}

export default DebounceSearch