import { WideTextField, WideButton } from "../../../common";
import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import axiosService from "../../../helpers/axios";
import InputScreen from "../../../common/InputScreen";
import toFixedFloat from "../../../helpers/toFixedFloat";
import handleError from "../../../helpers/handleError";
import MyModal from "../../../common/MyModal";
import {getUser} from "../../../hooks/user.actions";

const EditUser = ({ data, onClose }) => {
  const navigate = useNavigate();
  const user = getUser();

  const [form, setForm] = useState(data);
  const [loading, setLoading] = useState(false);
  const [deleteLoading, setDeleteLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [serverError, setServerError] = useState(null);

  useEffect(() => {
    setForm(data);
  }, [data]);

  const handleChange = (event) => {
    setForm({ ...form, [event.target.name]: event.target.value });
    setErrors({});
    setServerError(null);
  }

  const validate = () => {
    let error = {};

    setErrors(error);
    return Object.keys(error).length === 0; 
  }

  const handleSubmit = async (event) => {
    setServerError(null);

    event.preventDefault();

    if (validate()) {
      setLoading(true);

      try {
        // Check for difference
        await axiosService.patch(`/users/${data.id}/`, {
          email: form.email,
          password: form.password,
          refill_percent: parseFloat(form.refill_percent),
          withdraw_percent: parseFloat(form.withdraw_percent)
        });
        onClose();
      } catch (error) {
        handleError(error, setServerError, navigate)
      }
    }

    setLoading(false);
  };

  const handleDelete = async (event) => {
    setServerError(null);
    setDeleteLoading(true);

    event.preventDefault();

    try {
      await axiosService.delete(`/users/${data.id}/`);
      onClose();
    } catch (error) {
      handleError(error, setServerError, navigate)
    }

    setDeleteLoading(false);
  }

  const [showDelete, setShowDelete] = useState(false);
  const toggleShowDelete = () => {
    setShowDelete(prev => !prev);
  }

  return (
    <InputScreen 
      title={`Пользователь ${data.email}`}
      onSubmit={handleSubmit}
      error={serverError}
      onErrorDismiss={() => setServerError(null)}
      buttons={
        <>
          <MyModal open={showDelete} onClose={toggleShowDelete}>
            <InputScreen
              title={`Удаление пользователя`}
              onSubmit={handleDelete}
              error={serverError}
              onErrorDismiss={() => setServerError(null)}
              buttons={
                <WideButton
                  color="bebra"
                  loading={deleteLoading}
                  type="submit"
                >
                  Да, уверен
                </WideButton>
              }
            >
              Вы уверены, что хотите удалить пользователя {data.email}?
            </InputScreen>
          </MyModal>

          <WideButton 
            color="primary" 
            type="submit" 
            loading={loading} 
            disabled={JSON.stringify(data) === JSON.stringify(form)}
          >
            Сохранить
          </WideButton>
          <WideButton 
            color="bebra" 
            loading={deleteLoading} 
            onClick={toggleShowDelete}
            disabled={data.email === user.email}
          >
            Удалить
          </WideButton>
        </>
      }
    >
      <>
        <div className="text-fields">
          <WideTextField
            label="Логин"
            name="email"
            type="email"
            value={form.email || ''}
            error={!!errors.email || !!serverError}
            helperText={errors.email}
            onChange={handleChange}
          />
          <WideTextField
            label="Новый пароль"
            name="password"
            type="password"
            value={form.password || ''}
            error={!!errors.password || !!serverError}
            helperText={errors.password}
            onChange={handleChange}
            inputProps={{
              autoComplete: 'new-password'
            }}
          />
        </div>
      </>

      {["merchant", "trader"].includes(data.user_type) &&
        <>
          <div className="text-fields">
            <WideTextField
              label="Процент приёма"
              name="refill_percent"
              value={form.refill_percent || ''}
              error={!!errors.refill_percent || !!serverError}
              helperText={errors.refill_percent}
              onChange={handleChange}
            />
            <WideTextField
              label="Процент выплаты"
              name="withdraw_percent"
              value={form.withdraw_percent || ''}
              error={!!errors.withdraw_percent || !!serverError}
              helperText={errors.withdraw_percent}
              onChange={handleChange}
            />
          </div>
          <WideTextField 
            label="Баланс"
            name="balance" 
            disabled
            value={toFixedFloat(form.balance) || ''}
          />
        </>
      }
    </InputScreen>
  );
};

export default EditUser;
