import {Add, Mail} from "@mui/icons-material";
import {Paper, IconButton, MenuItem, FormControl, InputLabel, Select, Badge} from "@mui/material";
import {useContext, useState} from "react";
import {useLocation} from "react-router-dom";
import {CreatePopup} from "../modals";
import {getUser} from "../../../hooks/user.actions";
import useScreenWidth from "../../../hooks/useScreenWidth";
import {DebounceSearch} from "../../../common";
import CustomSelect from "./CustomSelect";
import UpdatesContext from "../context/UpdatesContext";
import {pages} from "../../../data/pages";
import CustomSelectItem from "./CustomSelectItem";

const ActionTab = ({filters, params, onParamsChange, onUpdateClick, onCreated}) => {
  const page = useLocation().pathname.slice(1);
  const userType = getUser().user_type;
  const showCreateButton = pages[page].canCreate.includes(userType);
  const showUpdateButton = pages[page].updateButton;

  const {extraSmallScreen} = useScreenWidth();

  const handleChange = (event) => {
    onParamsChange({ ...params, [event.target.name]: event.target.value, page: 1 });
  };

  const handleFilterChange = (event) => {
    const selectedFilter = filters.find(item => item.label === event.target.value);
    onParamsChange({ ...params, filter: selectedFilter, page: 1 });
  };

  const [showCreatePopup, setShowCreatePopup] = useState(false);
  const toggleCreatePopup = () => setShowCreatePopup(prev => !prev);

  const { newRequests } = useContext(UpdatesContext);

  return (
    <Paper className="action-tab" elevation={4}>
      <div className="action-tab-left-section">
        <div className="filters">
          {extraSmallScreen ? (
            <FormControl>
              <InputLabel>Фильтр</InputLabel>
              <Select
                label="Фильтр"
                name="filter"
                size="small"
                value={params.filter.label}
                onChange={handleFilterChange}
                sx={{ borderRadius: '8px' }}
              >
                {filters.map((option) => (
                  <MenuItem key={option.label} value={option.label}>
                    {option.label}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
          ) : (
            <CustomSelect
              name="filter"
              value={params.filter}
              onChange={handleChange}
              renderValue={(selected) => selected.label}
            >
              {filters.map((option) => (
                <CustomSelectItem key={option.label} value={option}>
                  {option.label}
                </CustomSelectItem>
              ))}
            </CustomSelect>
          )}
        </div>
        {showCreateButton &&
          <>
            <CreatePopup
              show={showCreatePopup}
              onClose={() => {
                toggleCreatePopup();
                onCreated();
              }}
            />
            <IconButton color="inherit" onClick={toggleCreatePopup}>
              <Add color="disabled"/>
            </IconButton>
          </>
        }
      </div>

      <div className="action-tab-right-section">
        {showUpdateButton && (
          <IconButton onClick={onUpdateClick}>
            <Badge color="warning" badgeContent={newRequests.length} invisible={newRequests.length <= 0}>
              <Mail color="disabled"/>
            </Badge>
          </IconButton>
        )}

        <DebounceSearch onChange={(newValue) => onParamsChange({ ...params, search: newValue })}/>
      </div>
    </Paper>
  );
};

export default ActionTab;
