import {useTheme} from "@mui/material";
import SecondaryWindow from "./SecondaryWindow";

const SecondaryWindows = ({ children }) => {
  const theme = useTheme();

  return (
    <div className='secondary-windows'>
      <SecondaryWindow>
        <span style={{color: theme.palette.primary.main}}>Реквизиты получателя в каждой заявке разные</span>,
        если при переводе указаны верные реквизиты, деньги зачислятся автоматически
      </SecondaryWindow>
      <SecondaryWindow>
        Зачисление денег на счет происходит автоматически при поступлении <span
        style={{color: theme.palette.primary.main}}>точной суммы по реквизитам заявки, без учета комисии банка</span>
      </SecondaryWindow>
      <SecondaryWindow>
        Если вы будете создавать <span style={{color: theme.palette.primary.main}}>заявки без оплаты</span>, то
        ваша учетная запись может быть заблокирована.
      </SecondaryWindow>
    </div>
  )
}

export default SecondaryWindows