import {formatTime} from "../../../helpers/formatTime";
import QuickDelete from "./formatting/QuickDelete";
import TablePage from "./TablePage";

const Tokens = () => {
  const filters = [
    { label: 'Все', value: {} }
  ]

  const columns = [
    { label: 'Название', element: (row) => row.name },
    { label: 'Истекает', element: (row) => formatTime(row.exp) },
    { label: 'Действие', element: (row) => <QuickDelete row={row}/> }
  ]

  return (
    <TablePage
      title="API токены"
      filters={filters}
      columns={columns}
    />
  );
};

export default Tokens;