import React, {useEffect, useState} from 'react';
import {Account, Bank, Fee, RequestStatus} from "./formatting";
import {getUser} from "../../../hooks/user.actions";
import Amount from './formatting/Amount';
import {Button} from "@mui/material";
import {formatTime} from "../../../helpers/formatTime";
import ConfirmButton from "./formatting/ConfirmButton";
import InputScreen from "../../../common/InputScreen";
import {WideButton} from "../../../common";
import MyModal from "../../../common/MyModal";
import axiosService from "../../../helpers/axios";
import TablePage from "./TablePage";
import {History} from "../forms";

const Requests = () => {
  const userType = getUser()?.user_type

  const filters = [
    { label: 'Все', value: {} },
    { label: 'Исполнен', value: { status: 'PD' } },
    { label: 'Ожидается', value: { status: 'WP' } },
    { label: 'В обработке', value: { status: 'CC' } },
    { label: 'Отменен', value: { status: 'FD' } }
  ]

  const columns= [
    { label: 'ID', width: "120px", element: (row) => row.request_proprietary_id },
    { label: 'Внешний ID', element: (row) => row.merchant_request_id },
    ...(["admin", "operator"].includes(userType) ? [
      { label: 'От кого', width: "120px", element: (row) => row.merchant_email },
      { label: 'Кому', element: (row) => row.trader_email }
    ]: []),
    ...(!["merchant"].includes(userType) ? [
      { label: 'Реквизит', width: "120px", element: (row) => <Account card_number={row.card_number} full_name={row.full_name}/> },
      { label: 'Банк', element: (row) => <Bank bank_name={row.bank_name}/> },
    ]: []),
    { label: 'Сумма', element: (row) => <Amount amount_initial={row.initial_amount} amount_to_pay={row.amount_to_pay}></Amount> },
    ...(["admin"].includes(userType) ? [
      { label: 'Комиссия', element: (row) => <Fee profit={row.profit} loss={row?.loss}/> }
    ]: []),
    ...(["trader"].includes(userType) ? [
      { label: 'Прибыль', element: (row) => <Fee profit={row.profit}/> }
    ]: []),
    { label: 'Создание', element: (row) => formatTime(row?.created) },
    { label: 'Статус', element: (row) => <RequestStatus status={row.status}/> },
    { label: 'Закрытие', element: (row) => formatTime(row?.closed) },
    { label: 'Действие', width: "140px", element: (row) =>
      userType === "merchant" ?
        <Button
          variant="outlined"
          size="small"
          disableElevation
          sx={{ textTransform: "none" }}
        >
          Отправить проверку
        </Button> :
        <div style={{display: 'flex', flexDirection: 'column', gap: '5px'}}>
          <ConfirmButton status={row.status} row={row} handleClick={handleQABClick}/>
          <Button
            variant="outlined"
            size="small"
            color="primary"
            disableElevation
            sx={{ textTransform: "none" }}
            onClick={(event) => handleHistoryClick(event, row)}
          >
            История
          </Button>
        </div>
    }
  ]

  const [selectedRow, setSelectedRow] = useState(null);

  const [loading, setLoading] = useState(false);
  const [serverError, setServerError] = useState(null);

  const handleSubmit = async (event) => {
    event.stopPropagation();
    event.preventDefault();

    setLoading(true);

    try {
      await axiosService.post(`/requests/approve/`, {
        request_proprietary_id: selectedRow.request_proprietary_id,
      });
      toggleShowConfirm(event);
    } catch (error) {
      console.log(error);
    }

    setLoading(false);
  };

  const [showConfirm, setShowConfirm] = useState(false);
  const toggleShowConfirm = () => {
    setShowConfirm(!showConfirm);
  }

  const handleQABClick = async (event, row) => {
    event.stopPropagation();
    event.preventDefault();

    setSelectedRow(row);

    toggleShowConfirm();
  };

  const handleHistoryClick = async (event, row) => {
    event.stopPropagation();
    event.preventDefault();

    setHistory([]);
    setSelectedRow(row);
    fetchHistory(row.id);

    toggleShowHistory();
  };

  const [showHistory, setShowHistory] = useState(false);
  const toggleShowHistory = () => {
    setShowHistory(prev => !prev);
  }

  const [history, setHistory] = useState([]);

  // useEffect(() => {
  //   if(selectedRow !== null) {
  //     fetchHistory();
  //   }
  // }, [selectedRow]);

  const fetchHistory = async (id) => {
    try {
      const response = await axiosService.get(`/requests/stats/${id}/`);
      setHistory(response.data)
    } catch (error) {
      console.log(error);
    }
  }

  return (
    <>
      <MyModal open={showConfirm} onClose={toggleShowConfirm}>
        <InputScreen
          title={"Подтверждение"}
          onSubmit={handleSubmit}
          error={serverError}
          onErrorDismiss={() => setServerError(null)}
          buttons={
            <WideButton color="success" type="submit" loading={loading}>
              Подтвердить
            </WideButton>
          }
        >
          {`Вы уверены, что хотите подтвердить заявку ${selectedRow?.request_proprietary_id} на сумму ${selectedRow?.amount_to_pay} по реквизитам ${selectedRow?.card_number} в ${formatTime(selectedRow?.created)}?`}
        </InputScreen>
      </MyModal>

      <MyModal open={showHistory} onClose={toggleShowHistory}>
        <InputScreen
          title={"История"}
          subtitle={selectedRow?.request_proprietary_id}
          onSubmit={() => {}}
          error={null}
          onErrorDismiss={() => setServerError(null)}
          // buttons={<></>}
        >
          <History history={history}/>
        </InputScreen>
      </MyModal>

      <TablePage
        title="Заявки"
        filters={filters}
        columns={columns}
      />
    </>
  );
};

export default Requests;